<template>
  <view style="height: 100%;width: 100%" :style="{
		'--active-font-color':activeColor
	}">
    <view class="zb-dropdown-btn" @click="show(true)">
      <view class="zb-dropdown-btn_con" :class="[{
        'active_title':this.selectValue
      }]">
        <slot name="content">
           <view class="zb_drop_header" >
             <slot name="title"><view class="zb_drop_header_text"><text :style="[{
              color:!this.selectValue?'':activeColor
            }]">{{title}}</text></view></slot>
               <text class="iconfont" :class="[isShow?'icon-arrow-up':'icon-arrow-down']"></text>
           </view>
        </slot>

      </view>
    </view>
    <template v-if="isShow">
      <view class="mask" @click.stop="hide" :style="[style]" :class="{mask__visible:mask}">
        <view class="zb-dropdown_content" @click.stop>
          <slot >
            <template v-if="type==='simple'">
              <view  :class="[{
                    item_dropdown_horizontal:mode==='horizontal'&&type==='simple'
                  }]">
                <view class="item_dropdown"
                      :class="[{
                          active_item:item.select
                        }]"
                      @click.stop="handlerAction(item,index)"
                      v-for="item,index in transData" :key="index">
                  <text :style="{
                        color:item.select?activeColor:''
                      }">{{ item.label }}</text>
                  <view
                      class="iconfont icon-select-bold" v-if="item.select&&mode==='vertical'" :style="[{
                color:activeColor
              }]"></view>
                </view>
              </view>
            </template>
            <template v-else-if="type.includes('withTitle')">
              <view class="zb_drop_with_title"

                    :style="[
                  {
                    maxHeight:maxHeight
                  }
              ]">
                <view class="zb_drop_with_tab" v-if="type==='withTitleTab'">
                  <scroll-view style="height: 100%" scroll-y >
                    <view v-for="item,index in transWithData"
                          class="item_tab_child"
                          :class="[{
                          itemTabActive:itemTabActive===index
                        }]"
                          @click="itemTabClick(item,index)"
                    >{{item.label}}</view>
                  </scroll-view>

                </view>
                <view class="zb_drop_with_tab_right">
                  <scroll-view style="height: 100%" scroll-y :scroll-into-view="scrollIntoIds">
                    <view v-for="itemWrap,i in transWithData"
                          :id="`tab${i}`"
                          class="zb-with-title">
                      <view class="title">{{itemWrap.label}}</view>
                      <view  class="item_dropdown_horizontal">
                        <view class="item_dropdown"
                              :class="[{
                          active_item:item.select
                        }]"
                              @click.stop="handlerAction(item,index,itemWrap)"
                              v-for="item,index in itemWrap.childrenData" :key="index">
                          <text :style="{
                        color:item.select?activeColor:''
                      }">{{ item.label }}</text>
                        </view>
                      </view>
                    </view>
                  </scroll-view>
                </view>
              </view>

            </template>


          </slot>
          <view class="zb-dropdown-footer" v-if="(footer&&multiple)||type.includes('withTitle')">
            <slot name="footer">
              <view class="zb-dropdown-footer_inner">
                <view class="zb-btn zb-btn_reset" @click="reset">重置</view>
                <view class="zb-btn zb-btn_config" @click="config">确定</view>
              </view>
            </slot>
          </view>
        </view>
      </view>
    </template>
  </view>
</template>

<script>
export default {
  name:"zb-dropdown",
  props:{
    dropHeaderClass:{
      type:String,
      default:'',
    },
    // simple 简单模式  withTitle 带标题 withTitleTab 带标题和tab切换
    type:{
      type:String,
      default:'simple',
    },
    maxHeight:{
      type:String,
      default:'auto',
    },
    mode:{
      type:String,
      // 默认垂直vertical   水平horizontal
      default:'vertical',
    },
    visible:{
      type: Boolean,
      default: false
    },
    activeColor:{
      type:String,
      //default:'#fa4913',
    },
    data:{
      type:Array,
      default:()=>[]
    },
    content:{
      type:String,
      default:''
    },
    multiple:{
      type:Boolean,
      default:false
    },
    footer:{
      type:Boolean,
      default:true
    },
  },
  watch:{
    content:{
      handler(val){
        this.title = val
      },
      immediate:true,
    },
    visible:{
      handler(val){
        this.isShow = val
        if(val){
          this.time = setTimeout(()=>{
            this.mask = true
          },300)
          this.getTop()
        }
      },
      immediate:true,
    },
    data:{
      handler(val){
        this.initData()
      },
      deep:true,
      immediate:true,
    },
    isShow:{
      handler(val){
        if(val){
          this.getTop()
        }
        this.$emit('update:visible', val)
      },
      immediate:true,
    }
  },
  data() {
    return {
      scrollIntoIds:'',
      title:'',
      selectValue:null,
      isShow:false,
      transData:[],
      transWithData:[],
      mask:false,
      style:{},
      time:null,
      pushTime:[],
      itemTabActive:null
    };
  },
  mounted(){

  },
  destory(){

  },
  methods:{
    getTop(){
      this.$nextTick(()=>{
        uni.createSelectorQuery().in(this).select('.zb-dropdown-btn').boundingClientRect((data)=>{
          this.style = {
            top:(data.top+data.height+3+uni.getSystemInfoSync().windowTop)+'px'
          }
        }).exec()
      })
    },
    itemTabClick(item,index){
      this.itemTabActive = index
      this.scrollIntoIds = `tab${index}`
      console.log('scrollIntoIds',this.scrollIntoIds)
    },
    show(){
      // if(!this.data.length) {
      //   this.isShow = false
      //   return false
      // }
      if(this.isShow){
        this.hide()
      }else {
        this.isShow = true
        this.time = setTimeout(()=>{
          this.mask = true
        },50)
        this.pushTime.push(this.time)
      }
    },
    hide(){
      this.isShow = false
	  // this.$emit('update:visible', false)
      this.mask = false
      this.pushTime.forEach(item=>{
        clearTimeout(item)
      })
      this.pushTime = []
    },
    handlerAction(item,index,itemWrap){
      if(this.type==='simple'){
        if(this.multiple){
          item.select = !item.select
        }else {
          this.transData.forEach(item=>{
            item.select = false
          })
          item.select = true
          this.isShow = false
          this.title = item.label
          this.selectValue = item.value
        }
      }else if(this.type.includes('withTitle')){
        if(itemWrap.multiple){
          item.select = !item.select
        }else {
          itemWrap.childrenData.forEach(ite=>{
            ite.select = false
          })
          item.select = true
        }
      }
    },
    deepClone(obj){
      let weakMap = new WeakMap()
      function clone (obj){
        if(obj==null){return obj}
        if(obj instanceof Date){ return new Date(obj) }
        if(obj instanceof RegExp){ return new RegExp(obj)}
        if(typeof obj !== 'object') return obj

        if(weakMap.get(obj)){
          return weakMap.get(obj)
        }
        var copy =  new obj.constructor
        weakMap.set(obj,copy)
        for(var key in obj){
          if(hasOwnProperty.call(obj, key)){
            var value = obj[key];
            copy[key] = clone(value);
          }
        }
        return copy;
      }
      return clone (obj)
    },
    config(){
      let arr = []
      if(this.type==='simple'){
        arr = this.transData.filter(item=>item.select)
      }else if(this.type.includes('withTitle')){
        let transWithData= this.deepClone(this.transWithData)
        arr = transWithData.map(item=>{
          let itemArr = item.childrenData.filter(ite=>ite.select)
          item.childrenData = itemArr
          return item
        })
      }
      if(arr.length){this.selectValue = true}
      this.$emit('ok',arr)
      this.hide()
    },
    initData(){
      let arr= this.deepClone(this.data)
      switch (this.type) {
        case 'simple':
          for(let item of arr){
            if(item.select==null){
              item.select = false
            }
          }
          this.transData = arr
          return
        case 'withTitle':
        case 'withTitleTab':
          for(let item of arr){
            if(item.childrenData&&item.childrenData.length){
              item.childrenData.forEach(it=>{
                if(it.select==null){
                  it.select = false
                }
              })
            }else {
              item.childrenData = []
            }

          }
          this.transWithData = arr
          return
      }

    },
    reset(){
      this.initData()

      this.selectValue = false
      console.log('===s=====',this.selectValue)
      this.$emit('reset')
      // this.hide()
    }
  }
}
</script>

